const API_KEY = "5ec76f143217881ba2e66dbcf2bf1849"; //https://home.openweathermap.org/ÏóêÏÑú Î∞õÏùÄ API key
const COORDS = "coords";
const weather = document.querySelector("#weather");

//ÏûêÎ∞îÏä§ÌÅ¨Î¶ΩÌä∏Í∞Ä Ï¢ãÏùÄ Ïù¥Ïú†! ÌéòÏù¥ÏßÄÎ•º Ï†ÑÏ≤¥ ÏÉàÎ°úÍ≥†Ïπ®ÌïòÏßÄ ÏïäÍ≥†ÎèÑ Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò¨ Ïàò ÏûàÏùå!!

function getWeather(lat,lng){
  fetch( `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lng}&appid=${API_KEY}&units=metric`)
  //Ïûò Í∞ÄÏ†∏ÏôîÎäîÏßÄ ÌôïÏù∏ : Î∏åÎùºÏö∞Ï†ÄF12-Network-weather blahblah-Headers-Request URL
  .then(function(response){ // then : Îç∞Ïù¥ÌÑ∞Í∞Ä ÏÑúÎ≤ÑÎ°úÎ∂ÄÌÑ∞ ÏôÑÏ†ÑÌûà fetchÎêú Îã§ÏùåÏóê~ Ìï®ÏàòÎ•º Ï∂úÎ†•ÌïòÍ≤†Îã§
        return response.json()
      })
      .then(function(json){ //thenÏùÑ Ï§ëÏ≤©ÏÇ¨Ïö©!! fetchÍ∞Ä ÎêêÎäîÏßÄ Í∏∞Îã§Î¶¨Í≥†, Í∑∏ responseÍ∞Ä ÏôîÎäîÏßÄ Í∏∞Îã§Î¶¨Í≥†, Îã§ÎêòÎ©¥ JSONÏùÑ Ï∂úÎ†•Ìï¥Îùº!
        // console.log(json)
        const temparature = json.main.temp;
        const place = json.name;
        const weatherDes = json.weather[0].description;
        const weatherIconNm = json.weather[0].icon;
        const weatherIconImg = `https://s3-us-west-2.amazonaws.com/s.cdpn.io/162656/${weatherIconNm}.svg`;
        weather.innerHTML = `${place} : ${temparature}‚ÑÉ  <img class="city-icon" src="${weatherIconImg}" alt="${weatherDes}">`;

      })
}

function saveCoords(coordsObj) {
  localStorage.setItem(COORDS, JSON.stringify(coordsObj));
  //ÏÇ¨Ïö©Ïûê ÌòÑÏû¨ Ï¢åÌëúÎ•º Îã§Î•∏ Ìï®ÏàòÎì§ÏùÑ ÌÜµÌï¥ Í∞ÄÏ†∏ÏôÄÏÑú handleGeoSuccess()Î•º ÌÜµÌï¥
  //      ObjectÎ°ú Ï†ÄÏû•Ìïú Îã§Ïùå, Îã§Ïãú stringÌôî Ìï¥ÏÑú local storageÏóê Ï†ÄÏû•ÌïòÍ∏∞.
}

function handleGeoSuccess(position) { //askForCoordsÏùò getCurrentPosition()Ïóê ÌïÑÏöîÌïú Í∞í1
  //ÏúÑÎèÑÏôÄ Í≤ΩÎèÑÎ•º APIÎ•º ÌÜµÌï¥ Î∂àÎü¨ÏôÄÏÑú ObjectÎ°ú Ï†ÄÏû•ÌïòÍ∏∞
  // console.log(position)
  const latitude = position.coords.latitude; //ÌòÑÏû¨ÏúÑÏπòÏùò ÏúÑÎèÑÍ∞íÎßå Ï†ÄÏû•
  const longitude = position.coords.longitude; //ÌòÑÏû¨ÏúÑÏπòÏùò Í≤ΩÎèÑÍ∞íÎßå Ï†ÄÏû•
  const coordsObj = {
    latitude, // latitude = latitude (ÏúÑÏóê Ï†ïÏùòÌï¥Ï§ÄÍ±∞Îûë Í∞ôÏùÄÏù¥Î¶ÑÏùò keyÎ•º ÏßÄÏ†ïÌïòÍ∏∞)
    longitude, // longitude = longitude (ObjectÏùò valueÍ∞íÎèÑ ÏúÑÏóê Ï†ïÏùòÌïú Ïù¥Î¶ÑÏùÑ Í∑∏ÎåÄÎ°ú ÏßÄÏ†ï)
  };
  saveCoords(coordsObj); //ÌòÑÏû¨ÏúÑÏπòÏùò [ÏúÑÎèÑ,Í≤ΩÎèÑ] ObjectÎ•º saveCoords()Ìï®ÏàòÏóê ÏûÖÎ†•
  getWeather(latitude,longitude); //getWeatherÌï®ÏàòÏóê ÌòÑÏû¨ÏúÑÏπòÏùò ÏúÑÎèÑ,Í≤ΩÎèÑÎ•º ÏûÖÎ†•(APIÏóêÏÑú ÎÇ†Ïî® fetchÌïòÍ∏∞ ÏúÑÌï®.)
  weather.classList.remove("hidden");
  weather.classList.add("show");
}

function handleGeoError() { //askForCoordsÏùò getCurrentPosition()Ïóê ÌïÑÏöîÌïú Í∞í2
  // weather.innerHTML = 'ÎÇ†Ïî®Ï†ïÎ≥¥ ÌôïÏù∏Î∂àÍ∞Ä'
  weather.classList.add("hidden");
}

function askForCoords() {
  //navigatorÏùò APIÎ•º ÏÇ¨Ïö©Ìï† Í≤É!
  navigator.geolocation.getCurrentPosition(handleGeoSuccess, handleGeoError); //ÌòÑÏû¨ÏúÑÏπòÏ†ïÎ≥¥ Î∞õÏïÑÏò§Í∏∞
} 

function loadCoords() {
               //Coords : Ï¢åÌëú
  const loadedCoords = localStorage.getItem(COORDS); //askForCoordsÎ•º ÌÜµÌï¥ Í∞ÄÏ†∏ÏôÄÏÑú saveCoordsÎ•º ÌÜµÌï¥ localStorageÏóê Ï†ÄÏû•Ìïú ÏúÑÏπò Ï¢åÌëúÍ∞í Í∞ÄÏ†∏Ïò§Í∏∞.
  if (loadedCoords === null) {
    askForCoords();
  } else { //Í∞ÄÏ†∏Ïò® Ï¢åÌëúÍ∞íÏù¥ ÏûàÏúºÎ©¥ stinrg -> objectÏúºÎ°ú Î≥ÄÌôòÌï¥Ï£ºÍ≥† ÏõêÌïòÎäî Í∞í Í∞ÄÏ†∏ÏôÄÏÑú getWeatherÏóê ÎÑ£Í≥† Ïã§Ìñâ.
    const parseCoords = JSON.parse(loadedCoords);
    getWeather(parseCoords.latitude,parseCoords.longitude);
  }
}

function init() {
  loadCoords();
}

init();